<template>
  <div id="back"></div>
  <button id="btn1" @click="onChangedModel(1)">鱼</button>
  <button id="btn2" @click="onChangedModel(2)">桥</button>
  <button id="btn3" @click="onChangedModel(3)">马</button>
  <button id="btn4" @click="onChangedModel(4)">树</button>

  <div id="loadingBack" v-if="isShow" >
    <img src="../public/file/loading.gif" alt="" id="loadImg">
  </div>
</template>

<script>

import * as THREE from "three";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import * as TWEEN from 'tween.js'
import { SimplifyModifier } from 'three/examples/jsm/modifiers/SimplifyModifier'

// var flag = false;//是否开始变换
var container
// var stats;
var camera, scene, renderer, geometry
var particles
var material
var glist = [];// glist 点阵数组
var around   // 环境点组
var aroundMaterial
var aroundPoints
var objIndex = 0;// 当前点阵模型index

export default {
  name: 'App',
  components: {

  },
  data() {
    return {
      isShow:false
    }
    
  },
  mounted() {
    this.init();
    this.animate()
  },

  methods: {

    init() {
      // renderer 的承载容器
      container = document.createElement('div');
      document.body.appendChild(container);
      // 初始化相机
      camera = new THREE.PerspectiveCamera(105, window.innerWidth / window.innerHeight, 10, 10000);
      camera.position.z = 100;
      camera.position.y = 60
      // 初始化场景
      scene = new THREE.Scene();
      scene.fog = new THREE.FogExp2(0x000000, 0.001);// 雾化
      //初始化renderer
      renderer = new THREE.WebGLRenderer();
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.setSize(window.innerWidth, window.innerHeight);
      container.appendChild(renderer.domElement);

      var oController = new OrbitControls(camera, renderer.domElement);
      oController.update();

      this.loadStartPoints(30000)

      // 加载模型
      // this.loadModel();

    },

    loadStartPoints(pointArr) {
      // 初始化geometry
      geometry = new THREE.Geometry();
      around = new THREE.Geometry();
      // 初始化贴图
      var textureLoader = new THREE.TextureLoader();
      textureLoader.crossOrigin = '';
      var mapDot = textureLoader.load('./file/gradientpoint.png');  // 圆点

      const geoNum = pointArr

      //初始变换点组
      for (let i = 0; i < geoNum; i++) {

        var vertex = new THREE.Vector3();
        vertex.x = 800 * Math.random() - 400;
        vertex.y = 800 * Math.random() - 400;
        vertex.z = 800 * Math.random() - 400;

        geometry.vertices.push(vertex);

        geometry.colors.push(new THREE.Color(1, 1, 1));

      }

      material = new THREE.PointsMaterial({ 
        size: 0.3, 
        sizeAttenuation: true, 
        color: 0xffffff, 
        transparent: true, 
        opacity: 1,
        map:mapDot });

      material.vertexColors = THREE.VertexColors;
      particles = new THREE.Points(geometry, material);
      scene.add(particles);

      //环境点组
      let arrNum = 2000

      //初始变换点组
      for (let i = 0; i < arrNum; i++) {

        var vertex1 = new THREE.Vector3();
        vertex1.x = 800 * Math.random() - 400;
        vertex1.y = 800 * Math.random() - 400;
        vertex1.z = 800 * Math.random() - 400;

        around.vertices.push(vertex1);
        // color.setRGB(Math.random()*1,Math.random()*1,Math.random()*1)

        around.colors.push(new THREE.Color(Math.random()*1,Math.random()*1,Math.random()*1));

      }
      // console.log(geometry);
      aroundMaterial = new THREE.PointsMaterial({ size: 0.4, sizeAttenuation: true, color: 0xffffff, transparent: true, opacity: 1, map: mapDot});

      aroundMaterial.vertexColors = THREE.VertexColors;
      aroundPoints = new THREE.Points(around, aroundMaterial);
      scene.add(aroundPoints);
    },

    loadModel() {

      let objName
      // 放大倍数
      let scale = 1

        if (objIndex === 1) {
          objName = 'afish.gltf'
          scale = 2000
        } else if (objIndex === 2) {
          objName = 'bridge.gltf'
          scale = 10

        }else if (objIndex === 3) {
          objName = 'horse.gltf'
          scale = 20

        }else if (objIndex === 4) {
          objName = 'tree.gltf'
          scale = 10
        }

        
        let fileName = './file/' + objName
      // DamagedHelmet.glb      bridge.gltf
      var loader = new GLTFLoader()
      let _this = this
      loader.load(fileName, function (gltf) {
        
        var gltfScene = gltf.scene
        console.log(gltfScene.children[0].children);

        let contuneNum = 0      // 循环次数
        let inIndex = gltfScene.children[0].children.length > 0 ? gltfScene.children[0].children.length:1

        glist = []
        // var verticesArr = child.geometry.attributes.position.array

        gltfScene.traverse(function (schild) {
          if (schild.isMesh) {
            //geo.rotateX(Math.PI / 2);
            
            while (schild.geometry.attributes.position.array.length > 75000) {
              // 簡化網格
              const simplified = schild.clone();
              simplified.material = simplified.material.clone();
              simplified.material.flatShading = true;
              const count = Math.floor(simplified.geometry.attributes.position.count * 0.05); // number of vertices to remove
              const modifier = new SimplifyModifier();
              simplified.geometry = modifier.modify(simplified.geometry, count);
              schild = simplified
              
            }
            contuneNum++

            var verticesArr = schild.geometry.attributes.position.array

            
            // 創建vec3
            for (let i = 0; i < verticesArr.length / 3; i++) {
              let vectorz = new THREE.Vector3(verticesArr[i*3]*scale, verticesArr[i*3 + 1]*scale, verticesArr[i*3 + 2]*scale)
              glist.push(vectorz)
            }
            if(inIndex === contuneNum) {
              _this.isShow = false
              _this.tweenObj()
            }
            
          }
        })

      })
    },

    onChangedModel(index) {
      this.isShow = true
      objIndex = index
      this.loadModel()
    },

    tweenObj() {

      geometry.vertices.forEach(function (e, i) {
				var length = glist.length;
				var o = glist[i % length];
				new TWEEN.Tween(e).to({
					x: o.x,
					y: o.y,
					z: o.z
				}, 1000).easing(TWEEN.Easing.Exponential.In).delay(1000 * Math.random()).start();
			})

    },

    animate() {
      requestAnimationFrame(this.animate);

      TWEEN.update();

      camera.lookAt(scene.position);
      // geometry.colors.forEach(function(color){
      //   color.setRGB(Math.random()*1,Math.random()*1,Math.random()*1)
      // })

      geometry.verticesNeedUpdate = true;

      renderer.render(scene, camera);
    }
  }

}
</script>

